import "./index.scss";
import { Button, Divider, Tooltip } from "antd";
import { useHomeStore } from "../../Stores/Home";
import { GiDrippingStar, GiVideoConference, GiClockwork } from "react-icons/gi";
import ScheduleModal from "./Common/ScheduleModal";

const Home = () => {
  const [, { create, setVisible }] = useHomeStore();
  function beep() {
    var snd = new Audio(
      "data:audio/wav;base64,"
    );
    snd.play();
  }

  return (
    <div className="autoHome">
      <div className="auto_bg">
        <img
          alt="cover"
          src="https://gomeet.io/assets/img/_illustration/landingpage.png"
        ></img>
      </div>
      <div className="button_div">
        <h2>
          Meet with your <GiDrippingStar />
          <span>friends</span>
        </h2>

        <div className="btn_wrap">
          <Tooltip placement="top" title={"Password will be 123456"}>
            <Button onClick={() => create()} type="primary" size={"large"}>
              <GiVideoConference style={{ marginRight: "10px" }} /> Start
              connecting now
            </Button>
          </Tooltip>
          <Divider>Or</Divider>
          <Button
            onClick={() => {
              beep();
              setVisible(true);
            }}
            type="primary"
            size={"large"}
          >
            <GiClockwork style={{ marginRight: "10px" }} /> Schedule a meet
          </Button>
        </div>
      </div>
      <ScheduleModal />
    </div>
  );
};

export default Home;
